let burger = document.querySelector(".burger")
let body = document.querySelector("body")
let menuCloseBtn = document.querySelector(".menu__close")

if (burger) {
    burger.onclick = function() {
        body.classList.add("_open")
        body.classList.add("fixed-body")
    }
}
if (menuCloseBtn) {
    menuCloseBtn.onclick = function() {
        body.classList.remove("_open")
        body.classList.remove("fixed-body")
    }
}

let menu = document.querySelector(".menu")

menu.onmouseover = function () {
    menu.classList.add("_expanded")
    menu.classList.add("_extra")
}
menu.onmouseleave = function () {
    menu.classList.remove("_expanded")
    menu.classList.remove("_extra")
}
